
.vfl-has-label {
  position: relative;
}
.vfl-label {
  position: absolute;
  top: 0;
  left: 0.1em;
  overflow: hidden;
  font-family: sans-serif;
  font-size: 0.8em;
  color: #aaa;
  text-overflow: ellipsis;
  white-space: nowrap;
  opacity: 0;
  transition: all 0.2s ease-out;
}
.vfl-label-on-input {
  top: -1.3em;
  opacity: 1;
}
.vfl-label-on-focus {
  color: #0074d9;
}
.vfl-has-label input:focus,
.vfl-has-label textarea:focus {
  outline: 0;
}



